.packages{
    margin-top: 20px;
    margin-bottom: 20px;
}

.card{

    min-height: 550px;
    text-align: left;
    padding: 10px;
    margin: 0px 20px;
    margin-bottom: 10px;
    border-radius: 30px;
    background-color: #9e8934 !important;
    color: rgb(233, 227, 227);
    transition: transform .7s;
    font-family: monospace;
    width: 310px;

}
.card:hover{
    transform: scale(1.03);
    box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;
    transition: transform .1s ease;

}
.card a{
    border-radius: 20px;
    border: 5px solid #dcd1d1;
    font-size: 15px;
    font-weight: 600;
    background-color: #2b2449;
}
.card h5{
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 0 5px 0 5px;
    text-align: center;
    font-weight: 400;
    font-size: 50px;
}
.card h6{
    font-size: 150%;
}
.card p{
    font-weight: 200;
    font-size: 90%;
}
.card img{
    height: 300px;
}
.card li{
    list-style: none;
}
.features{
    margin-bottom: 35px;
}

.img{
    display: flex;
    align-items: center;
    flex-direction: column;

}
.img svg{
    height: 156px;
    width: 175px;
    color: rgb(223, 222, 183);
}