 
  .marquee {
    overflow: hidden;
  }
  
  .marquee div { 
    position: relative; 
    overflow: hidden; 
    width: 10%; 
   right: 0px !important;
    animation: righttoleft 5s infinite ease-out;
  }

  @keyframes righttoleft {
    0%   {  left: 100%;  } 
    100% {  left: -15%;  ;}
  }
  