.instagram svg:hover {
    fill: purple;
}

.twitter svg:hover {
    fill: #0975f2;;
}

.youtube svg:hover {
    fill: red;
}


.message{
    background: -webkit-linear-gradient(left,#82009f 0,#5d34af 50%,#00ceab 100%);
    background: linear-gradient(to right,#471452 0,#5e37ad 50%,#0bc5a5 100%);
    color: var(--theme-white);
    min-height: 30px;
}



.nav {
    background-color: #3a24f0;
    padding: 10px;
    text-align: center;
    width: 100%;
    z-index: 10  !important;
    /* Pay special attention here! */
    position: sticky;
    top: 0px;

    box-shadow: 0px 5px 10px 0px rgba(192.75000000000003, 192.75000000000003, 192.75000000000003, 0.5);
    transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
    padding: 0px 0px 12px 0px;
    margin-bottom: 50px;
    max-height: 50px;
    transition: all .5s ease-out;
  }
  .nav img{
    width: 10%;
    margin: -30px;
  }

  .nav [name='brand']{
    margin-left: 30px;
    font-size: 30px;
  }

  
  .nav a {
    text-decoration: none;
    margin: 0px 10px;
    font-weight: bold;
  }
  
