.appointment {
    padding: 20px;
    background: aliceblue;
    border-radius: 8px;
    margin: 30px 0px 30px 0px;
    text-align: start;
    padding-bottom: 40px;
}

.appointment [name='header'] {
    font-size: 20px;
    font-weight: bolder;
    text-align: start;
    margin-bottom: 15px;
    margin-top: 15px;
}


.appointment [name='form'] input {
    margin-bottom: 10px;
}

.appointment [name='form'] button {
    margin-top: 10px;
    border-radius: 3px;
    font-size: 17px;
    font-weight: bold;
    background: var(--theme-main-color);
    border: none;
}

.appointment [name='form'] button:hover {
    
    background: var(--theme-main-color-dark);
}

.appointment [name='form'] button:focus {
    
    color: white;
}

.desc{
    text-align: start;
    display: flex;
    flex-wrap: wrap;
    align-content: start;
    margin-top: 30px;    
}
.desc div:nth-child(1) {
    font-size: 3.5rem;
    opacity: 0.7;
    font-weight: 500;
}
.desc div:nth-child(2) {
    font-size: 2rem;
    font-weight:bold;
}

.desc div:nth-child(3) {
    font-size: 2rem;
    color: var(--theme-main-color);
    font-weight: bold;
}